import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineChart from "@/components/charts/LineChart"
import BarIndicator from "@/components/charts/BarChart"

import {CONFIG, Data, IndicatorData, IndicatorType} from "@/shared/data";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import FilterMonths from "@/components/filters/months/index.vue";
import FilterAreas from "@/components/filters/areas/index.vue";
import {ChartType, getChartOptions, getLineDataset} from "@/shared/charts";

@Component({
    components: {
        'v-filter-months': FilterMonths,
        'v-filter-areas': FilterAreas,
        'v-line-indicator': LineChart,
        'v-bar-indicator': BarIndicator
    },
})
export default class AccidentChart extends Vue {
    public selectedArea = REPUBLIC_CODE
    public selectedMonth = CONFIG.accident.month || DEFAULT_MONTH

    public preparingData = !Data.isMonthFetched(IndicatorType.ACCIDENT, this.selectedMonth)

    public indicators = [7, 8, 9, 10]
    public data: IndicatorData[] = []

    async mounted() {
        await this.fetchData()
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.ACCIDENT, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ACCIDENT, this.selectedMonth)
        }
        this.data.splice(0, this.data.length)
        this.indicators.forEach(indicator => this.data.push(Data.getYearsData(IndicatorType.ACCIDENT, indicator, this.selectedArea, this.selectedMonth)))
        this.preparingData = false
    }

    public getData(indicator: number): IndicatorData {
        return this.data.find(dataItem => dataItem.indicator === indicator) || {divider: 1, labels: [], values: []}
    }

    get charts() {
        return [this.chart7, this.chart8910]
    }

    get chart7() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.getData(7).labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.getData(7).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.accident.registered').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart8910() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(8).labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.accident.dead').toString(), '#e62228', this.getData(10).values),
                    getLineDataset(this.$t('xLabels.accident.wounded').toString(), '#f39c12', this.getData(9).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.accident.victims').toString(), this.$t('yLabels.quantity').toString())
        }
    }

}
