import {Model, Component, Vue, Emit} from 'vue-property-decorator'

@Component
export default class FilterQuarters extends Vue {

    @Model('change')
    private quarter!: string

    get quarters() {
        const quarters = []
        for (let quarter = 1; quarter <= 4; quarter++) {
            quarters.push({value: quarter * 3, text: this.$t('quarter')+ ' ' + quarter})
        }
        return quarters
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
