import {Component, Emit, Model, Prop, Vue} from 'vue-property-decorator'
import FilterRegions from "@/components/filters/regions/index.vue";
import {getAreas, REPUBLIC_CODE} from "@/shared/onstants";

@Component({
    components: {
        'v-filter-regions': FilterRegions
    },
})
export default class FilterAreas extends Vue {
    @Model('change', {default: REPUBLIC_CODE})
    public area!: string

    @Prop({default: false})
    public court!: boolean

    @Prop({default: false})
    public transport!: boolean

    public region = REPUBLIC_CODE

    mount() {
        if (this.area.length > 4) {
            this.region = this.area.substr(0,4)
        } else {
            this.region = this.area
        }
    }

    get areas() {
        const areas = [{value: this.region, text: this.$i18n.t('all_areas').toString()}]
        if (this.region !== '19') {
            getAreas(this.region).map(areaId => areas.push({
                value: areaId,
                text: this.$i18n.t(`areas.${areaId}`).toString()
            }))
        }
        return areas
    }

    onRegionChanged() {
        this.change(this.areas[0].value)
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
