import Component from 'vue-class-component'
import {Prop, Vue} from 'vue-property-decorator'
import {IndicatorValue} from "@/shared/data";
import {REGIONS} from "@/shared/onstants";

@Component
export default class RepublicMap extends Vue {

    @Prop()
    public data!: IndicatorValue[]

    @Prop({default: true})
    public transport!: boolean

    @Prop({default: true})
    public army!: boolean

    public value(valueArea: string): string {
        const result = this.data.find(item => item.area === valueArea)
        if (typeof result !== 'undefined') {
            return result.value.toLocaleString()
        }
        return this.$t('na').toString()
    }

    get regions() {
        return REGIONS
    }

    public onAreaClick(area: string) {
        this.$emit("onAreaClick", area)
    }

}
