import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import FilterAreas from "@/components/filters/areas/index.vue";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import {FileInfo, Files} from "@/shared/files";
import {CONFIG, Data, IndicatorType, YEARS} from "@/shared/data";
import FilterMonths from "@/components/filters/months/index.vue";

@Component({
    components: {
        'v-filter-months': FilterMonths,
        'v-filter-areas': FilterAreas
    }
})
export default class Statistics extends Vue {
    public preparingData = false

    public selectedMonth = CONFIG.criminal.month || DEFAULT_MONTH
    public selectedArea = REPUBLIC_CODE
    public tabIndex = YEARS.length - 1

    public data: FileInfo[] = []

    async mounted() {
        await this.fetchData()
    }

    get years() {
        return YEARS
    }

    private async fetchData() {
        if (!Files.isYearFetched(this.years[this.tabIndex])) {
            this.preparingData = true
            await Files.fetchData(this.years[this.tabIndex])
        }
        this.data = Files.getFilesData(this.years[this.tabIndex], this.selectedMonth, this.selectedArea)
        console.log(this.data)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

}
