import {Model, Component, Vue, Emit} from 'vue-property-decorator'

@Component
export default class FilterCivilIndicators extends Vue {

    @Model('change')
    private indicator!: string

    get indicators() {
        const messages: any = this.$i18n.messages[this.$i18n.locale]
        return messages.filters.civil
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
