import {Component, Emit, Model, Prop, Vue} from 'vue-property-decorator'
import {Data} from "@/shared/data";
import {REGIONS} from "@/shared/onstants";

@Component
export default class FilterRegions extends Vue {

    @Model('change')
    public region!: string

    @Prop({default: false})
    public court!: boolean

    @Prop({default: false})
    public transport!: boolean

    get regions() {
        const regions = [{value: "19", text: this.$i18n.t('republic').toString()}]

        if (this.court) {
            regions.push({value: "1111", text: this.$i18n.t('supremeCourt').toString()})
        }

        if (this.transport) {
            // TODO regions.push({value: "1970", text: ' '})
        }

        REGIONS.map(regionId => regions.push({
            value: regionId,
            text: this.$i18n.t(`regions.${regionId}`).toString()
        }))
        return regions
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
