import Papa from 'papaparse'

export async function getData(year: string): Promise<[]> {
    return await fetch(`/files/${year}/files.csv`)
        .then((response) => response.text())
        .then((value: string) => Papa.parse<any>(value))
        .then((result: any) => result.data.slice(1))
}

export interface FileInfo {
    month: number
    area: string
    code: string
    filename: string
}

export class Files {
    private static files: Map<string, FileInfo[]> = new Map();

    public static async fetchData(year: string) {
        let data = await getData(year)
        const info = data.map(file => {
            const fileInfo: FileInfo = {month: Number(file[0]), area: file[1], code: file[2], filename: file[3]}
            return fileInfo
        })
        this.files.set(year, info)
    }

    public static isYearFetched(year: string): boolean {
        return this.files.has(year)
    }

    public static getFilesData(year: string, month: number, area: string): FileInfo[] {
        const data = this.files.get(year)
        if (data === undefined) {
            return []
        }
        return data.filter(file => file.month === month && file.area === area)
    }

}
