import {Component, Emit, Model, Vue} from 'vue-property-decorator'
import {YEARS} from "@/shared/data";

@Component
export default class FilterYears extends Vue {

    @Model('change')
    private year!: string

    get years() {
        return YEARS
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
