import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineChart from "@/components/charts/LineChart"
import BarIndicator from "@/components/charts/BarChart"
import {CONFIG, Data, IndicatorData, IndicatorType} from "@/shared/data";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import {ChartType, getBarDataset, getChartOptions, getLineDataset} from "@/shared/charts";
import FilterRegions from "@/components/filters/regions/index.vue";

@Component({
    components: {
        'v-line-indicator': LineChart,
        'v-bar-indicator': BarIndicator,
        'v-filter-regions': FilterRegions
    },
})
export default class ErsopChart extends Vue {
    public selectedRegion = REPUBLIC_CODE
    public selectedMonth = CONFIG.ersop.month || DEFAULT_MONTH

    public preparingData = !Data.isMonthFetched(IndicatorType.ERSOP, this.selectedMonth)

    public indicators = [1, 2, 3, 4, 5, 6, 7, 8, 9]
    public data: IndicatorData[] = []

    async mounted() {
        await this.fetchData()
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.ERSOP, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ERSOP, this.selectedMonth)
        }
        this.data.splice(0, this.data.length)
        this.indicators.forEach(indicator => this.data.push(Data.getYearsData(IndicatorType.ERSOP, indicator, this.selectedRegion, this.selectedMonth)))
        this.preparingData = false
    }

    public getData(indicator: number): IndicatorData {
        return this.data.find(dataItem => dataItem.indicator === indicator) || {divider:1, labels: [], values: []}
    }

    get charts() {
        return [this.chart12, this.chart34, this.chart7658, this.chart9]
    }

    get chart12() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(1).labels,
                    datasets: [
                        getBarDataset(this.$t('xLabels.total').toString(), '#3498db', this.getData(1).values),
                        getBarDataset(this.$t('xLabels.ersop.subjects').toString(), '#52be80', this.getData(2).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.ersop.all-registered').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart34() {
        const data3 = Data.getYearsData(IndicatorType.ERSOP, 3, this.selectedRegion, this.selectedMonth)
        const data4 = Data.getYearsData(IndicatorType.ERSOP, 4, this.selectedRegion, this.selectedMonth)
        return {
            type: ChartType.BAR,
            data: {
                labels: data3.labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.ersop.all-denied').toString(), '#3498db', data3.values),
                    getBarDataset(this.$t('xLabels.ersop.subjects').toString(), '#52be80', data4.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.ersop.registration-denied').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart7658() {
        const data7 = Data.getYearsData(IndicatorType.ERSOP, 7, this.selectedRegion, this.selectedMonth)
        const data6 = Data.getYearsData(IndicatorType.ERSOP, 6, this.selectedRegion, this.selectedMonth)
        const data5 = Data.getYearsData(IndicatorType.ERSOP, 5, this.selectedRegion, this.selectedMonth)
        const data8 = Data.getYearsData(IndicatorType.ERSOP, 8, this.selectedRegion, this.selectedMonth)
        return {
            type: ChartType.BAR,
            data: {
                labels: data7.labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.ersop.micro').toString(), '#ee5213', data8.values),
                    getBarDataset(this.$t('xLabels.ersop.small').toString(), '#efd44d', data5.values),
                    getBarDataset(this.$t('xLabels.ersop.middle').toString(), '#52be80', data6.values),
                    getBarDataset(this.$t('xLabels.ersop.large').toString(), '#3498db', data7.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.ersop.by-private-property-type').toString(), this.$t('yLabels.quantity').toString(), true)
        }
    }

    get chart9() {
        const data9 = Data.getYearsData(IndicatorType.ERSOP, 9, this.selectedRegion, this.selectedMonth)
        return {
            type: ChartType.LINE,
            data: {
                labels: data9.labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#3498db', data9.values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.ersop.unscheduled').toString(), this.$t('yLabels.quantity').toString())
        }
    }


}
