import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import {CONFIG, Data, IndicatorType, YEARS} from "@/shared/data";
import FilterMonths from "@/components/filters/months/index.vue";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import FilterAccidentIndicators from "@/components/filters/indicators/accident/index.vue";

@Component({
    components: {
        'v-filter-months': FilterMonths,
        'v-filter-accident-indicators': FilterAccidentIndicators
    }
})
export default class AccidentTable extends Vue {
    private selectedMonth = CONFIG.accident.month || DEFAULT_MONTH
    private selectedIndicator = 7

    private preparingData = !Data.isMonthFetched(IndicatorType.ACCIDENT, this.selectedMonth)

    public values = []

    get fields() {
        const fields: any = [
            {
                key: 'regionTitle',
                label: this.$i18n.t('region').toString()
            }
        ]
        YEARS.forEach(year => fields.push({
            key: year,
            formatter: 'numberFormatter',
            tdClass: 'text-right',
            thClass: 'text-right'
        }))
        return fields
    }

    get items() {
        return this.values.map((row: any) => {
            if (row.region !== REPUBLIC_CODE) {
                row.regionTitle = this.$i18n.t(`regions.${row.region}`).toString()
            } else {
                row.regionTitle = this.$i18n.t('republic').toString()
            }
            return row
        })
    }

    public numberFormatter(value: number) {
        return value.toLocaleString()
    }

    async mounted() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.ACCIDENT, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ACCIDENT, this.selectedMonth)
        }
        this.values = Data.getTableData(IndicatorType.ACCIDENT, this.selectedIndicator, this.selectedMonth)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

}
