import Component from 'vue-class-component'
import {Vue, Watch} from 'vue-property-decorator'
import RepublicMap from "@/components/maps/republic/index.vue";
import {CONFIG, Data, IndicatorType, IndicatorValue} from "@/shared/data";
import RegionMap from "@/components/maps/region/index.vue";
import {DEFAULT_QUARTER_MONTH, DEFAULT_YEAR, REPUBLIC_CODE} from "@/shared/onstants";
import FilterYears from "@/components/filters/years/index.vue";
import FilterAdministrativeIndicators from "@/components/filters/indicators/administrative/index.vue";
import FilterQuarters from "@/components/filters/quarters/index.vue";

@Component({
    components: {
        'v-filter-years': FilterYears,
        'v-filter-quarters': FilterQuarters,
        'v-filter-administrative-indicators': FilterAdministrativeIndicators,
        'v-republic-map': RepublicMap,
        'v-region-map': RegionMap
    }
})export default class AdministrativeMap extends Vue {
    private selectedQuarter = CONFIG.administrative.month || DEFAULT_QUARTER_MONTH
    private selectedYear = CONFIG.administrative.year || DEFAULT_YEAR
    private selectedIndicator = 1

    private preparingData = !Data.isMonthFetched(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)

    private currentRegion = REPUBLIC_CODE

    public values: IndicatorValue[] = []

    async mounted() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)
        }
        this.values = Data.getAreasData(IndicatorType.ADMINISTRATIVE, this.selectedIndicator, this.selectedYear, this.currentRegion, this.selectedQuarter)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    public onRepublicClick() {
        this.currentRegion = REPUBLIC_CODE
    }

    public onAreaClick(area: string) {
        this.currentRegion = area
    }

    @Watch('currentRegion')
    async onRegionChanged() {
        await this.fetchData()
    }

}
