import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineChart from "@/components/charts/LineChart"
import BarIndicator from "@/components/charts/BarChart"

import {CONFIG, Data, IndicatorData, IndicatorType} from "@/shared/data";
import FilterRegions from "@/components/filters/regions/index.vue";
import {DEFAULT_MONTH, DEFAULT_QUARTER_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import {ChartType, getBarDataset, getChartOptions, getLineDataset} from "@/shared/charts";

@Component({
    components: {
        'v-line-indicator': LineChart,
        'v-bar-indicator': BarIndicator,
        'v-filter-regions': FilterRegions
    },
})
export default class CivilChart extends Vue {
    public selectedRegion = REPUBLIC_CODE
    public selectedMonth = CONFIG.civil.month || DEFAULT_MONTH

    public preparingData = !Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)

    public indicators = [1, 2, 3, 4, 5]
    public data: IndicatorData[] = []

    async mounted() {
        await this.fetchData()
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CIVIL, this.selectedMonth)
        }
        this.data.splice(0, this.data.length)
        this.indicators.forEach(indicator => this.data.push(Data.getYearsData(IndicatorType.CIVIL, indicator, this.selectedRegion, this.selectedMonth)))
        this.preparingData = false
    }

    public getData(indicator: number): IndicatorData {
        return this.data.find(dataItem => dataItem.indicator === indicator) || {divider:1, labels: [], values: []}
    }

    get charts() {
        return [this.chart1, this.chart23, this.chart45]
    }

    get chart1() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.getData(1).labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.getData(1).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.civil.received').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart23() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(2).labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.civil.excited').toString(), '#3498db', this.getData(2).values),
                    getBarDataset(this.$t('xLabels.civil.reviewed').toString(), '#52be80', this.getData(3).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.civil.excited-and-considered').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart45() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(4).labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.civil.satisfied').toString(), '#52be80', this.getData(4).values),
                    getBarDataset(this.$t('xLabels.civil.denied').toString(), '#fd1111', this.getData(5).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.civil.satisfied-and-denied').toString(), this.$t('yLabels.quantity').toString())
        }
    }

}
