import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import RepublicMap from "@/components/maps/republic/index.vue";
import {CONFIG, Data, IndicatorType, IndicatorValue} from "@/shared/data";
import RegionMap from "@/components/maps/region/index.vue";
import {DEFAULT_MONTH, DEFAULT_YEAR, REPUBLIC_CODE} from "@/shared/onstants";
import FilterYears from "@/components/filters/years/index.vue";
import FilterCivilIndicators from "@/components/filters/indicators/civil/index.vue";

@Component({
    components: {
        'v-filter-years': FilterYears,
        'v-filter-civil-indicators': FilterCivilIndicators,
        'v-republic-map': RepublicMap,
        'v-region-map': RegionMap
    }
})export default class CivilMap extends Vue {
    private selectedMonth = CONFIG.civil.month || DEFAULT_MONTH
    private selectedYear = CONFIG.civil.year || DEFAULT_YEAR
    private selectedIndicator = 1

    private preparingData = !Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)

    private currentRegion = REPUBLIC_CODE

    public values: IndicatorValue[] = []

    async mounted() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CIVIL, this.selectedMonth)
        }
        this.values = Data.getAreasData(IndicatorType.CIVIL, this.selectedIndicator, this.selectedYear, this.currentRegion, this.selectedMonth)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

}
