import {Model, Component, Vue, Emit} from 'vue-property-decorator'

@Component
export default class FilterMonths extends Vue {

    @Model('change')
    private month!: string

    get months() {
        const months = []
        for (let month = 1; month <= 12; month++) {
            months.push({value: month, text: this.$t('months.' + month + '')})
        }
        return months
    }

    @Emit('change')
    change(value: string) {
        return value
    }

}
