import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineChart from "@/components/charts/LineChart"
import BarIndicator from "@/components/charts/BarChart"

import {CONFIG, Data, IndicatorData, IndicatorType} from "@/shared/data";
import {DEFAULT_MONTH, DEFAULT_QUARTER_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import FilterQuarters from "@/components/filters/quarters/index.vue";
import FilterAreas from "@/components/filters/areas/index.vue";
import {ChartType, getBarDataset, getChartOptions, getLineDataset} from "@/shared/charts";

@Component({
    components: {
        'v-filter-quarters': FilterQuarters,
        'v-filter-areas': FilterAreas,
        'v-line-indicator': LineChart,
        'v-bar-indicator': BarIndicator
    },
})
export default class AdministrativeChart extends Vue {
    public selectedArea = REPUBLIC_CODE
    public selectedQuarter = CONFIG.administrative.month || DEFAULT_QUARTER_MONTH

    public preparingData = !Data.isMonthFetched(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)

    public indicators = [1, 2, 3, 4, 5, 6]
    public data: IndicatorData[] = []

    async mounted() {
        await this.fetchData()
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ADMINISTRATIVE, this.selectedQuarter)
        }
        this.data.splice(0, this.data.length)
        this.indicators.forEach(indicator => this.data.push(Data.getYearsData(IndicatorType.ADMINISTRATIVE, indicator, this.selectedArea, this.selectedQuarter)))
        this.preparingData = false
    }

    public getData(indicator: number): IndicatorData {
        return this.data.find(dataItem => dataItem.indicator === indicator) || {divider: 1, labels: [], values: []}
    }

    get charts() {
        return [this.chart124, this.chart3, this.chart56]
    }

    get chart124() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(1).labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.administrative.registered').toString(), '#3498db', this.getData(1).values),
                    getBarDataset(this.$t('xLabels.administrative.attracted').toString(), '#52be80', this.getData(2).values),
                    getBarDataset(this.$t('xLabels.administrative.penalties-imposed').toString(), '#d9fc30', this.getData(4).values),
                ]
            },
            options: getChartOptions(this.$t('xLabels.administrative.offenses').toString(), this.$t('yLabels.quantity').toString(), false, 2)
        }
    }

    get chart3() {
        return {
            type: ChartType.LINE,
            data: {
                labels: this.getData(3).labels,
                datasets: [
                    getLineDataset(this.$t('xLabels.total').toString(), '#2e86c1', this.getData(3).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.administrative.terminated').toString(), this.$t('yLabels.quantity').toString())
        }
    }

    get chart56() {
        return {
            type: ChartType.BAR,
            data: {
                labels: this.getData(5).labels,
                datasets: [
                    getBarDataset(this.$t('xLabels.administrative.superimposed').toString(), '#3498db', this.getData(5).values),
                    getBarDataset(this.$t('xLabels.administrative.collected').toString(), '#52be80', this.getData(6).values)
                ]
            },
            options: getChartOptions(this.$t('xLabels.administrative.amount-of-fines').toString(), this.$t('yLabels.sum').toString())
        }
    }

}
