import Component from 'vue-class-component'
import {Vue, Watch} from 'vue-property-decorator'
import RepublicMap from "@/components/maps/republic/index.vue";
import {CONFIG, Data, IndicatorType, IndicatorValue} from "@/shared/data";
import RegionMap from "@/components/maps/region/index.vue";
import FilterYears from "@/components/filters/years/index.vue";
import FilterMonths from "@/components/filters/months/index.vue";
import FilterCriminalIndicators from "@/components/filters/indicators/criminal/index.vue";
import {DEFAULT_MONTH, DEFAULT_YEAR, REPUBLIC_CODE} from "@/shared/onstants";

@Component({
    components: {
        'v-filter-years': FilterYears,
        'v-filter-months': FilterMonths,
        'v-filter-criminal-indicators': FilterCriminalIndicators,
        'v-republic-map': RepublicMap,
        'v-region-map': RegionMap
    }
})
export default class CriminalMap extends Vue {
    private selectedMonth = CONFIG.criminal.month || DEFAULT_MONTH
    private selectedYear = CONFIG.criminal.year || DEFAULT_YEAR
    private selectedIndicator = 1

    private preparingData = !Data.isMonthFetched(IndicatorType.CRIMINAL, this.selectedMonth)

    private currentRegion = REPUBLIC_CODE

    public values: IndicatorValue[] = []

    async mounted() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.CRIMINAL, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CRIMINAL, this.selectedMonth)
        }
        this.values = Data.getAreasData(IndicatorType.CRIMINAL, this.selectedIndicator, this.selectedYear, this.currentRegion, this.selectedMonth)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

    public onRepublicClick() {
        this.currentRegion = REPUBLIC_CODE
    }

    public onAreaClick(area: string) {
        console.log(area)
        this.currentRegion = area
    }

    @Watch('currentRegion')
    async onRegionChanged() {
        await this.fetchData()
    }

}
