import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import {CONFIG, Data, IndicatorType, YEARS} from "@/shared/data";
import {DEFAULT_MONTH, REPUBLIC_CODE} from "@/shared/onstants";
import FilterCivilIndicators from "@/components/filters/indicators/civil/index.vue";

@Component({
    components: {
        'v-filter-civil-indicators': FilterCivilIndicators
    }
})
export default class CivilTable extends Vue {
    private selectedMonth = CONFIG.civil.month || DEFAULT_MONTH
    private selectedIndicator = 1

    private preparingData = !Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)

    public values = []

    get fields() {
        const fields: any = [
            {
                key: 'regionTitle',
                label: this.$i18n.t('region').toString()
            }
        ]
        YEARS.forEach(year => fields.push({key: year, formatter: 'numberFormatter', tdClass: 'text-right', thClass: 'text-right'}))
        return fields
    }

    get items() {
        return this.values.map((row: any) => {
            if (row.region !== REPUBLIC_CODE) {
                row.regionTitle = this.$i18n.t(`regions.${row.region}`).toString()
            } else {
                row.regionTitle = this.$i18n.t('republic').toString()
            }
            return row
        })
    }

    public numberFormatter(value: number) {
        return value.toLocaleString()
    }

    async mounted() {
        await this.fetchData()
    }

    private async fetchData() {
        if (!Data.isMonthFetched(IndicatorType.CIVIL, this.selectedMonth)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CIVIL, this.selectedMonth)
        }
        this.values = Data.getTableData(IndicatorType.CIVIL, this.selectedIndicator, this.selectedMonth)
        this.preparingData = false
    }

    async onFilterChanged() {
        await this.fetchData()
    }

}
