import Component from 'vue-class-component'
import {Prop, Vue} from 'vue-property-decorator'
import {IndicatorValue} from "@/shared/data";
import {AREAS} from "@/shared/onstants";

@Component
export default class RegionMap extends Vue {

    @Prop()
    public data!: IndicatorValue[]

    @Prop()
    public region!: string

    public value(valueArea: string): string {
        const result = this.data.find(item => item.area === valueArea)
        if (typeof result !== 'undefined') {
            return result.value.toLocaleString()
        }
        return this.$t('na').toString()
    }

    get areas() {
        return AREAS.filter(area => area.substr(0,4) === this.region.toString())
    }

    get id() {
        return 'svgRegion'+ this.region.toString()
    }

    get titleName() {
        return this.$t('regions.' + this.region.toString()).toString()
    }

    get titleValue() {
        return this.value(this.region.toString())
    }

    public onRepublicClick() {
        this.$emit("onRepublicClick")
    }

}
